import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { ManageComponent } from './manage.component';


const routes: Routes = [
  {
    path: '',
    component: ManageComponent
  },
  {
    path: 'products',
    loadChildren: () => import('./products/products.module').then(m => m.ProductsModule)
  },
  {
    path: 'reports',
    loadChildren: () => import('./reports/reports.module').then(m => m.ReportsModule)
  },
  {
    path: 'partner',
    loadChildren: () => import('./partner/partner.module').then(m => m.PartnerModule)
  },
  {
    path: 'invoice',
    loadChildren: () => import('./invoices/invoices.module').then(m => m.InvoicesModule)
  },
  {
    path: 'cashflow',
    loadChildren: () => import('./cashflow/cashflow.module').then(m => m.CashflowModule)
  },
  {
    path: 'stock',
    loadChildren: () => import('./stock/stock.module').then(m => m.StockModule)
  },
  {
    path: 'warranty',
    loadChildren: () => import('./warranty/warranty.module').then(m => m.WarrantyModule)
  },
  {
    path: 'delivery',
    loadChildren: () => import('./delivery/delivery.module').then(m => m.DeliveryModule)
  },
  {
    path: 'sales-program',
    loadChildren: () => import('./sales-program/sales-program.module').then(m => m.SalesProgramModule)
  },
  {
    path: 'tax',
    loadChildren: () => import('./tax/tax.module').then(m => m.TaxModule)
  },
  {
    path: 'printer',
    loadChildren: () => import('./printer/printer.module').then(m => m.PrinterModule)
  },
  {
    path: 'branch',
    loadChildren: () => import('./branch/branch.module').then(m => m.BranchModule)
  },
  {
    path: 'ecommerce',
    loadChildren: () => import('./ecommerce/ecommerce.module').then(m => m.EcommerceModule)
  },
  {
    path: 'socials-channel',
    loadChildren: () => import('./socials-channel/socials-channels.module').then(m => m.SocialsChannelsModule)
  },
  {
    path: 'design-barcode',
    loadChildren: () => import('./design-barcode/design-barcode.module').then(m => m.DesignBarcodeModule)
  }
]

@NgModule({
  declarations: [ManageComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes)
  ]
})
export class ManageModule { }
